/************ Projects Css ************/
.project {
    padding: 80px 0;
    position: relative;
    background-color: black;
}
.project h2 {
    font-size: 45px;
    font-weight: 700;
    text-align: center;
}
.project p {
    color: #B8B8B8;
    font-size: 18px;
    letter-spacing: 0.8px;
    line-height: 1.5em;
    margin: 14px auto 30px auto;
    text-align: center;
    width: 56%;
}
.project .nav.nav-pills {
    width: 72%;
    margin: 0 auto;
    border-radius: 50px;
    background-color: rgb(255 255 255 / 10%);
    overflow: hidden;
}
.project .nav.nav-pills .nav-item {
    width: 50%;
}
.project .nav.nav-pills .nav-link {
    background-color: transparent;
    border-radius: 0;
    padding: 17px 0;
    color: #fff;
    width: 100%;
    font-size: 17px;
    letter-spacing: 0.8px;
    font-weight: 500;
    position: relative;
    transition: 0.3s ease-in-out;
    text-align: center;
    z-index: 0;
}
.project .nav.nav-pills .nav-link::before {
    content: "";
    position: absolute;
    width: 0;
    height: 100%;
    background: linear-gradient(90.21deg, #AA367C -5.91%, #4A2FBD 111.58%);
    top: 0;
    left: 0;
    z-index: -1;
    transition: 0.3s ease-in-out;
}
.project .nav.nav-pills .nav-link.active::before {
    width: 100% !important;
}
.project .nav.nav-pills .nav-link.active {
    border: 1px solid rgba(255, 255, 255, 1);
}
.nav-link#projects-tabs-tab-first {
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 55px 0px 0px 55px;
}
.nav-link#projects-tabs-tab-second {
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 0 55px 55px 0;
}

.project-slider {
  padding: 20px 0;
  position: relative;
  margin: 0 40px; /* Adds space for arrows */
}

.project-slider .slick-list {
  overflow: hidden; /* Ensure no content spills outside visible bounds */
}

.project-slider .slick-slide {
  opacity: 0.4; /* Less visible for non-active slides */
  transform: scale(0.8); /* Shrink non-active slides */
  transition: all 0.3s ease-in-out;
}

.project-slider .slick-active {
  opacity: 1; /* Full visibility for focused slide */
  transform: scale(0.5); /* Restore size for focused slide */
}

.project-slider .slick-center {
  opacity: 1 !important; /* Full visibility for center slide */
  transform: scale(1) !important; /* Highlight center slide by increasing size */
  z-index: 2; /* Bring center slide to the foreground */
}

.project-slider .slick-arrow {
  z-index: 1000; /* Keep arrows above all other elements */
}

.project-slider .slick-prev {
  left: 10px; /* Adjust position of the left arrow */
}

.project-slider .slick-next {
  right: 10px; /* Adjust position of the right arrow */
}

.project-slider .slick-prev, .project-slider .slick-next {
  width: 40px; /* Ensure smaller arrows */
  height: 40px;
}

.proj-imgbx img {
  width: 100%; /* Increase size of project card images */
  height: auto;
  margin: 0 auto;
  display: block;
}

.project-slider-item {
  padding: 0; /* Remove extra padding around project cards */
  display: flex;
  justify-content: center;
  align-items: center;
}

.project-card {
  border-radius: 15px;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    transition: transform 0.3s ease;
}

.proj-imgbx {
    position: relative;
    overflow: hidden;
    border-radius: 15px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease;
}


.proj-imgbx:hover {
    transform: scale(1.05); /* Slight zoom on hover */
}

.proj-txtx {
    position: absolute;
    bottom: 0;
    background: rgba(0, 0, 0, 0.6);
    color: #fff;
    width: 100%;
    text-align: center;
    padding: 10px 0;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
}

.proj-imgbx:hover .proj-txtx {
    opacity: 1;
}
.background-image-right {
    top: 20%;
    position: absolute;
    bottom: 0;
    width: 35%;
    right: 0;
    z-index: -4;
}

/************ Projects Css ************/
.contact {
    background: linear-gradient(90.21deg, #AA367C -5.91%, #4A2FBD 111.58%);
    padding: 60px 0 200px 0;
}
.contact img {
    width: 92%;
}
.contact h2 {
    font-size: 45px;
    font-weight: 700;
    margin-bottom: 30px;
}
.contact form input, .contact form textarea {
    width: 100%;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 20px;
    color: #fff;
    margin: 0 0 8px 0;
    padding: 18px 26px;
    font-weight: 500;
    font-size: 18px;
    letter-spacing: 0.8px;
    transition: 0.3s ease-in-out;
}
.contact form input:focus, .contact form textarea:focus {
    background: rgba(255, 255, 255, 1);
    color: #121212;
}
.contact form input::placeholder, .contact form textarea::placeholder {
    font-size: 16px;
    font-weight: 400;
    color: #fff;
}
.contact form input:focus::placeholder, .contact form textarea:focus::placeholder {
    color: #121212;
    opacity: 0.8;
}
.contact form button {
    font-weight: 700;
    color: #000;
    background-color: #fff;
    padding: 14px 48px;
    font-size: 18px;
    margin-top: 25px;
    border-radius: 0;
    position: relative;
    transition: 0.3s ease-in-out;
}
.contact form button span {
    z-index: 1;
    position: relative;
}
.contact form button:hover {
    color: #fff;
}
.contact form button::before {
    content: "";
    background: #121212;
    width: 0;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    transition: 0.3s ease-in-out;
}
.contact form button:hover::before {
    width: 100%;
}

